import { useSelector } from "react-redux";
import { StyledNavLink } from "./AppBar.styled";

const AppBar = () => {
  const user = useSelector(state => state.auth.user);
  const isUserLoggedIn = Boolean(user?.token);
    return (
      <header>
        <nav>
          {isUserLoggedIn ?  <StyledNavLink to="/contacts"></StyledNavLink> :
            <>
              <StyledNavLink to="/"></StyledNavLink>
              <StyledNavLink to="/register"></StyledNavLink>
              <StyledNavLink to="/login"></StyledNavLink>
            </>
          }

         
          <hr />
        </nav>
      </header>
    );
}

export default AppBar;